import React from 'react';
import { Box, Skeleton, Stack, Container } from '@mui/material';
import { motion } from 'framer-motion';
import { styled, keyframes } from '@mui/material/styles';

// Enhanced loading animations
const shimmer = keyframes`
  0% { transform: translateX(-100%); }
  100% { transform: translateX(100%); }
`;

const LoadingCard = styled(Box)(({ theme }) => ({
  background: theme.palette.background.paper,
  borderRadius: theme.spacing(3),
  padding: theme.spacing(3),
  position: 'relative',
  overflow: 'hidden',
  border: `1px solid ${theme.palette.divider}`,
  '&::before': {
    content: '""',
    position: 'absolute',
    top: 0,
    left: 0,
    right: 0,
    bottom: 0,
    background: `linear-gradient(90deg, transparent, ${theme.palette.primary.main}10, transparent)`,
    animation: `${shimmer} 2s infinite`,
  }
}));

export const ProjectsLoadingSkeleton: React.FC = () => (
  <Container maxWidth="lg">
    <motion.div
      initial={{ opacity: 0 }}
      animate={{ opacity: 1 }}
      transition={{ duration: 0.5 }}
    >
      {/* Header skeleton */}
      <Box sx={{ textAlign: 'center', mb: 6 }}>
        <Skeleton variant="text" width="40%" height={60} sx={{ mb: 2, mx: 'auto' }} />
        <Skeleton variant="text" width="60%" height={30} sx={{ mx: 'auto' }} />
      </Box>

      {/* Metrics skeleton */}
      <Box sx={{ display: 'flex', gap: 3, mb: 6, justifyContent: 'center', flexWrap: 'wrap' }}>
        {[1, 2, 3, 4, 5].map((i) => (
          <LoadingCard key={i} sx={{ minWidth: 280, flex: '0 1 300px' }}>
            <Skeleton variant="circular" width={40} height={40} sx={{ mb: 2 }} />
            <Skeleton variant="text" width="60%" height={40} />
            <Skeleton variant="text" width="80%" height={20} />
          </LoadingCard>
        ))}
      </Box>

      {/* Projects grid skeleton */}
      <Box sx={{ display: 'flex', flexWrap: 'wrap', gap: 3, justifyContent: 'center' }}>
        {[1, 2, 3, 4, 5].map((i) => (
          <LoadingCard key={i} sx={{ width: 400, height: 600 }}>
            <Skeleton variant="rectangular" width="100%" height={200} sx={{ mb: 2 }} />
            <Skeleton variant="text" width="80%" height={30} sx={{ mb: 1 }} />
            <Skeleton variant="text" width="100%" height={60} sx={{ mb: 2 }} />
            <Stack direction="row" spacing={1} sx={{ mb: 2 }}>
              {[1, 2, 3].map((j) => (
                <Skeleton key={j} variant="rounded" width={60} height={25} />
              ))}
            </Stack>
            <Skeleton variant="text" width="40%" height={20} />
          </LoadingCard>
        ))}
      </Box>
    </motion.div>
  </Container>
);

export const PageLoadingSkeleton: React.FC = () => (
  <Container maxWidth="lg" sx={{ py: 8 }}>
    <motion.div
      initial={{ opacity: 0 }}
      animate={{ opacity: 1 }}
      transition={{ duration: 0.5 }}
    >
      <Box sx={{ textAlign: 'center', mb: 6 }}>
        <Skeleton variant="text" width="50%" height={80} sx={{ mb: 2, mx: 'auto' }} />
        <Skeleton variant="text" width="70%" height={40} sx={{ mx: 'auto' }} />
      </Box>
      
      {[1, 2, 3].map((i) => (
        <LoadingCard key={i} sx={{ mb: 4, p: 4 }}>
          <Stack direction="row" spacing={3}>
            <Skeleton variant="circular" width={80} height={80} />
            <Box sx={{ flex: 1 }}>
              <Skeleton variant="text" width="60%" height={30} sx={{ mb: 1 }} />
              <Skeleton variant="text" width="40%" height={20} sx={{ mb: 2 }} />
              <Skeleton variant="text" width="100%" height={60} />
            </Box>
          </Stack>
        </LoadingCard>
      ))}
    </motion.div>
  </Container>
);
